@use '../../../styles/mixins';

.imagesWrapper {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photoBackdrop {
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
}

.closeButton {
  position: absolute;
  color: #000;
  outline: 0;
  width: 32px;
  height: 32px;
  right: 5px;
  top: 5px;
  background-color: rgba(245, 245, 245, 0.455);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  font-size: 20px;
  align-items: center;

  @include mixins.mq(tablet) {
    width: 40px;
    height: 40px;
    right: 10px;
    top: 10px;
    font-size: 32px;
  }
  @include mixins.mq(desktop) {
    width: 44px;
    height: 44px;
    right: 10px;
    top: 10px;
  }
}

.photoContainer {
  position: relative;
  max-height: 96vh;
}

.photoContainer > img {
  margin: 0 auto;
}

.arrowButton {
  cursor: pointer;
  z-index: 1001;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  background-color: rgba(245, 245, 245, 0.586);
  border-radius: 50%;
  color: var(--text-color);
  transition: all 0.3s ease-in-out;
  &:hover {
    background-color: var(--accent-color);
  }
  &:hover svg {
    color: var(--icon-color);
  }
  &:hover {
    background-color: var(--accent-color);
  }
  @include mixins.mq(tablet) {
    width: 64px;
    height: 64px;
    background-color: rgba(245, 245, 245, 0.5);
  }
}

.arrowlinkRigth {
  margin-left: 6px;
}

.arrowlinkLeft {
  margin-right: 6px;
}

.arrowButtonLeft {
  @include mixins.mq(mobile-only) {
    position: absolute;
    top: 45%;
    left: 0;
  }
  @include mixins.mq(tablet) {
    margin-left: 10px;
  }
  @include mixins.mq(desktop) {
    margin-left: 140px;
  }
}

.arrowButtonRight {
  @include mixins.mq(mobile-only) {
    position: absolute;
    top: 45%;
    right: 0;
  }
  @include mixins.mq(tablet) {
    margin-right: 10px;
  }
  @include mixins.mq(desktop) {
    margin-right: 140px;
  }
}
